.page-header {
  height: 200px;
  background-color: #fff;
  background-image: url(../img/header-bg.png);
  background-position: center bottom;
  background-repeat: no-repeat; }
  .page-header a {
    color: #fff; }
  .page-header_container {
    display: flex;
    padding: 0 10px;
    align-items: center;
    justify-content: space-between; }
    @media screen and (min-width: 980px) {
      .page-header_container {
        max-width: 980px;
        margin: 0 auto;
        padding: 0 20px; } }
  .page-header_top {
    background: #38afa9;
    color: #fff; }
  .page-header_title {
    color: #38afa9;
    background: #fff;
    font-weight: 300;
    margin: 0;
    text-align: center; }
  .page-header_login {
    color: #fff;
    display: block; }
  .page-header_logo {
    display: block; }
